.wrapper{
    display: flex;
    flex-direction: column;
    position: fixed;
    margin-top: 190px;
    margin-left: 70px;
    color: black;
}

p{
    margin-bottom: 70px;
}

.icon{
    margin-top: 40px;
}

 a{
    padding-right: 10px;
    padding-bottom: 10px;
}

 a:hover{
    transition: all 1s ease 0s;
    transform: scale(1.02);
    color: #78797A;
}

 button {
    background-color: inherit;
    margin-top: 50px;
    color:black;
    font-size: 12px;
    border: none;
    padding-left: 0;
    cursor: pointer;
}
 button:hover, button:focus, button:active{
   outline: none;
}

